<template lang="pug">
    .subjects__content
        .subjects__title Предметы
        .subjects__container
            .subjects__filter
                .subjects__filter-item 
                    label.label__filter-subjects Выберите департамент:
                    select(v-model="selectedDepartmentId").subjects__filter-select
                        option(value=0) Все
                        option(:value='item.id' v-for='item in departments') {{item.name}}
            ul.subjects__list
                swiper.swiper(:options="swiperOption")
                    swiper-slide(v-for='item in slidesNumber')
                        li.subjects__item(v-for="subject, idx in filteredSubjects" :key="subject.id" v-if='(item-1)*steps <= idx && idx< item*steps')
                            subject-item(
                                :subject='subject'
                                )
                    .swiper-pagination.swiper-pagination-bullets(slot="pagination")
        button(@click.prevent='addSubject' id='addSubject').add__subject Добавить предмет
        add-subject-form(
                :departments = "departments"
                v-if='addSubjectFormVisible'
                v-on:eventNewSubject='eventNewSubject'
            )

</template>
<script>
import {mapState, mapActions} from "vuex";
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
export default {
    components: {
        subjectItem: () => import("../../admin/subjects/subject-item"),
        addSubjectForm: () => import("../../admin/subjects/add-subject-form"),
        Swiper,
        SwiperSlide
    },
    computed: {
        ...mapState("subjects",
        {
            subjects: state => state.subjects
        }
        ),
        ...mapState("departments",
        {
            departments: state => state.departments
        }
        ),
        filteredSubjects: function() {
            return this.subjects
            //filter by department
            .filter(subject=> {
                return this.selectedDepartmentId == 0 || subject.department.id == this.selectedDepartmentId;
            })
        },
        slidesNumber: function(){
            return (Math.floor(this.filteredSubjects.length/this.steps)==this.filteredSubjects.length/this.steps)?Math.floor(this.filteredSubjects.length/this.steps):Math.floor(this.filteredSubjects.length/this.steps)+1
        }
    },
    data () {
        return {
            selectedDepartmentId: 0,
            steps: 4,
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                    renderBullet(index, className) {
                    return `<span class="${className} swiper-pagination-bullet-custom">${index + 1}</span>`
                    }
                }
            },
            addSubjectFormVisible: false
        }
    },
    created() {
        this.fetchSubjects();
        this.fetchDepartments();
    },
    methods: {
        ...mapActions('subjects', ['fetchSubjects']),
        ...mapActions("departments", ["fetchDepartments"]),
        addSubject() {
            this.addSubjectFormVisible = !this.addSubjectFormVisible
            document.getElementById('addSubject').setAttribute("disabled", "true")

        },
        eventNewSubject() {
            this.addSubjectFormVisible=!this.addSubjectFormVisible
            document.getElementById('addSubject').removeAttribute("disabled")
      }
    }
}
</script>
